
.manage-photos {
  width: 100%;
  height: 100%;

  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;

  .manage-photos-container {
    display: flex;
    flex-flow: row;
    width: 50vw;
    margin-bottom: 20px;

    .half {
      width: 50%;
      padding: 20px;
    }

    .add-photo {
      .photo-form {
        form {
          display: flex;
          flex-flow: column;
        }

        .submit {
          display: flex;
          flex-flow: row;
          align-items: center;
        }
      }
    }

    .edit-photos {
      border-left: 1px solid gray;

      h2 {
        margin-bottom: 20px;
      }

      .entry-links {
        display: flex;
        flex-direction: column;

        a {
          margin: 3px 15px;
        }
      }
    }
  }
}
